import styled, { css } from "styled-components";
import { Title2, Title3, Text, InnerWrap } from "../../assets/styles/component";

import icon_bills from "../../assets/images/icon_bills.svg";
import icon_chat from "../../assets/images/icon_chat.svg";
import icon_shines from "../../assets/images/icon_shines.svg";
import icon_organization from "../../assets/images/icon_organization.svg";
import like from "../../assets/images/Like.svg";
import disLike from "../../assets/images/DisLike.svg";
import BadAd from "../../assets/images/BadAd.webp";
import BestAd from "../../assets/images/BestAd.webp";

import arrow from "../../assets/images/purpure_arrow.svg";
import { useEffect, useRef, useState } from "react";

const SCompare = styled.section`
  position: relative;
  padding: 70px 0;

  & > div {
    display: flex;
    flex-direction: column;
    align-items: center;

    & > p {
      text-align: center;
      margin-bottom: 70px;
    }
  }
`;

const Title = styled(Title2)`
  margin-bottom: 30px;
  font-size: 26px;
`;

const CompareBlock = styled.div`
  display: grid;
  grid-template-columns: 1fr;
  gap: 20px;
  margin-bottom: 120px;
  width: 100%;

  & > div {
    position: relative;
    margin-bottom: 20px;
  }
`;

const FrameBoxContainer = styled.div`
  position: relative;
  overflow: visible;

  &::after {
    content: "";
    position: absolute;
    top: -30%;
    left: -70%;
    width: 230%;
    height: 200%;
    z-index: -1;

    @media (max-width: 1300px) {
      width: 200%;
      left: -60%;
    }

    background: radial-gradient(
        25.27% 36.6% at 50% 50%,
        rgba(241, 2, 117, 0.524) 0%,
        rgba(241, 2, 117, 0) 100%
      ),
      radial-gradient(
        24.63% 36.71% at 37.85% 36.14%,
        #0202f188 0%,
        rgba(2, 2, 241, 0) 100%
      ),
      radial-gradient(
        19.47% 28.35% at 55.07% 60.96%,
        #ff429d83 0%,
        rgba(255, 66, 158, 0) 100%
      ),
      radial-gradient(
        21.4% 32.49% at 68.89% 47.03%,
        #cb1d8581 0%,
        rgba(203, 29, 133, 0) 100%
      );

    backdrop-filter: opacity(80%);
  }
`;

const Frame = styled.div`
  margin: 0 auto;
  position: relative;
  overflow: hidden;
  max-width: 999px;
  border-radius: 14px;
  outline: 5px solid #ffffff22;
  width: 100%;
  height: fit-content;
  max-width: 789px;
  min-height: 30vw;
  display: flex;
  justify-content: center;

  background: radial-gradient(
    240.7% 80.8% at 107.77% 65.48%,
    #a927ca 0%,
    #d72f75 41.67%,
    #e79079 76.04%,
    #e3592f 98.44%
  );

  img {
    width: 100%;
    border-radius: 14px;
  }
`;

const sLike = css`
  position: absolute;
  display: flex;
  align-items: center;
  gap: 7px;
  width: fit-content;
  height: fit-content;
  font-size: 24px;
  color: #fff;
  z-index: 3;
`;

const DisLike = styled.div`
  ${sLike}
  padding: 15px 42px 15px 22px;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.16);
  backdrop-filter: blur(11px);
  border-radius: 50px;
  font-size: 25px;
  align-self: flex-start;
  justify-self: flex-end;
  white-space: nowrap;
  transform: rotate(7.83deg) scale(0.65);

  top: -20%;
  left: -15%;

  @media (min-width: 450px) {
    top: -10%;
    left: -10%;
  }
`;

const Like = styled.div`
  ${sLike}
  padding: 15px 34px 15px 24px;
  background: radial-gradient(
      152.32% 158.02% at -13.45% 12.49%,
      #d83575 3.12%,
      #7e03f4 59.16%,
      #1010f2 100%
    ),
    #ffffff;
  box-shadow: inset 0px 0px 64.6636px rgba(255, 255, 255, 0.7);
  border-radius: 55px;
  transform: rotate(-8.37deg) scale(0.65);
  align-self: flex-end;
  justify-self: flex-end;

  bottom: -15%;
  right: -15%;

  @media (min-width: 450px) {
    right: -10%;
  }
`;

const Arrow = styled.img`
  position: absolute;
  right: 45%;
  top: 100%;
  width: 50%;

  @media (min-width: 450px) {
    right: 30vw;
    top: 80%;
    width: 30vw;
    transform: rotate(20deg);
  }
`;

const List = styled.ul`
  display: flex;
  flex-direction: column;
  color: #fff;
  gap: 10px;
  max-width: 220px;
  margin-bottom: 20px;

  li {
    position: relative;
    font-size: clamp(12px, 1.2vw, 14px);
    padding-left: 23px;

    &:before {
      position: absolute;
      content: "";
      top: 5px;
      width: 6px;
      height: 6px;
      left: 0px;
      border-radius: 10px;
      background-color: ${({ mark }) => (mark ? mark : `#363636`)};
    }
  }
`;

const BestList = styled.div`
  display: flex;
  justify-content: space-around;
  text-align: center;
  width: 100%;
  gap: 40px;
  flex-wrap: wrap;
`;

const Best = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 10px;
  width: 100%;

  & > div:nth-child(2) {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    height: 100%;

    h3 {
      font-size: 14px;
      text-align: left;
      margin-bottom: 10px;
    }

    p {
      opacity: 0.7;
      text-align: left;
    }
  }
`;

const BadImg = styled.img``;

const GoodImg = styled.img``;

const Icon = styled.div`
  border: 2px solid rgba(255, 255, 255, 0.1);
  box-shadow: inset 0px 0px 64.6636px rgba(255, 255, 255, 0.51);

  display: flex;
  justify-content: center;
  align-items: center;

  width: 63px;
  min-width: 63px;
  height: 69px;
  border-radius: 20px;

  img {
    width: 26px;
  }

  background-color: ${({ color }) => {
    switch (color) {
      case "blue":
        return `#0202F1;`;
      case "orange":
        return `#E3592F`;
      case "violet":
        return `#9C00E8`;
      case "yellow":
        return `#ECA356`;
    }
  }};
`;

const Compare = ({ language }) => {
  const [coordX, setCoordX] = useState(0);
  const [dragMv, setDragMv] = useState(false);

  useEffect(() => {
    if (coordX > 0) {
      MBtn.current.style.left = coordX - 500 + "px";
      Mv.current.style.left = coordX - 450 + "px";
      img.current.style.left = "-" + (coordX - 450) + "px";
    }
  }, [coordX]);

  const img = useRef(null);
  const MBtn = useRef(null);
  const Mv = useRef(null);

  return (
    <SCompare>
      <InnerWrap>
        <Title>Сравните сами</Title>
        <Text fix_size="12" size="12">
          Вместо обычных ссылок вы сможете интегрировать неограниченное
          количество стримов уникальный промо-формат
        </Text>

        <CompareBlock>
          <div>
            <Frame>
              <BadImg src={BadAd} alt="BackImg" loading="lazy" />
            </Frame>
            <DisLike>
              <img src={disLike} alt="Dislike" loading="lazy" /> Old
              Fashion
            </DisLike>
          </div>

          <List>
            <li>переход с видео (теряется ссылка, или контент)</li>
            <li>забывается после просмотра видео</li>
            <li>необходимо голосовое объявление</li>
            <li>замыленность формата (не реагируют на рекламу)</li>
          </List>

          <FrameBoxContainer>
            <Frame>
              <GoodImg src={BestAd} alt="BackImg" loading="lazy" />
            </Frame>
            <Like>
              <img src={like} alt="" loading="lazy" /> SocialStream
              <Arrow src={arrow} alt="" loading="lazy" />
            </Like>
          </FrameBoxContainer>

          <List mark="violet">
            <li>всегда на нужном месте не закрывают блоггера</li>
            <li>круто выглядят и привлекают внимание</li>
            <li>100% видимость, не нужнообъявлять голосом</li>
            <li>не отвлекает от просмотра контента</li>
          </List>
        </CompareBlock>

        <BestList>
          <Best>
            <Icon color="blue">
              <img src={icon_chat} alt="Like" loading="lazy" />
            </Icon>

            <div>
              <Title3>Больше вовлеченности</Title3>
              <Text fix_size="12" size="12">
                и показатели для достижения brand awarnece
              </Text>
            </div>
          </Best>

          <Best>
            <Icon color="orange">
              <img src={icon_shines} alt="" loading="lazy" />
            </Icon>

            <div>
              <Title3>Новизна формата и не замыленность</Title3>
              <Text fix_size="12" size="12">
                это интересно, вирально, уникально
              </Text>
            </div>
          </Best>

          <Best>
            <Icon color="violet">
              <img src={icon_organization} alt="" loading="lazy" />
            </Icon>

            <div>
              <Title3>Неорганиченная маштабируемость</Title3>
              <Text fix_size="12" size="12">
                ваша интеграция всегда в уместном формате и месте
              </Text>
            </div>
          </Best>

          <Best>
            <Icon color="yellow">
              <img src={icon_bills} alt="" loading="lazy" />
            </Icon>

            <div>
              <Title3>Оплата за фактический охват</Title3>
              <Text fix_size="12" size="12">
                вы получаете просмотры, а не факт интеграции
              </Text>
            </div>
          </Best>
        </BestList>
      </InnerWrap>
    </SCompare>
  );
};

export default Compare;
